.player {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: rgba(176, 237, 255, 0.3)
}
.time-control {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    input {
        width: 100%;
        -webkit-appearance: none;
        background: transparent;
        cursor: pointer;
    }
    p {
        padding: 1rem;
    }
}
.play-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 20%;
    svg {
        cursor: pointer;
    }

}
input[type='range']:focus{
    outline: none;
    
}
input[type='range']::-webkit-slider-thumb{
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
}
input[type='range']::-moz-range-thumb{
    -webkit-appearance: none;
    background: transparent;
    border: none;
}
.track{
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}
.animate-track {
    background: rgb(204,204,204);
    width: 100%;
    height: 1rem;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
}

@media screen and (max-width: 767px) {
    .time-control {
        width: 90%;
    }
    .play-control {
        width: 60%;
    }
}