@import url(https://fonts.googleapis.com/css2?family=Rambla:ital,wght@0,400;0,700;1,400&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Rambla';background-color:rgba(204,245,219,0.3)}h1,h2,h3{color:#545454}.App{transition:all 0.5s ease}.library-active{margin-left:30%}.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between;background-color:rgba(176,237,255,0.3)}.time-control{width:50%;display:flex;align-items:center;justify-content:center}.time-control input{width:100%;-webkit-appearance:none;background:transparent;cursor:pointer}.time-control p{padding:1rem}.play-control{display:flex;justify-content:space-between;align-items:center;padding:1rem;width:20%}.play-control svg{cursor:pointer}input[type='range']:focus{outline:none}input[type='range']::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px}input[type='range']::-moz-range-thumb{-webkit-appearance:none;background:transparent;border:none}.track{width:100%;height:1rem;position:relative;border-radius:1rem;overflow:hidden}.animate-track{background:#ccc;width:100%;height:1rem;position:absolute;top:0;left:0;transform:translateX(0%);pointer-events:none}@media screen and (max-width: 767px){.time-control{width:90%}.play-control{width:60%}}.song-container{min-height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.song-container img{width:20%;border-radius:50%}h2{padding:2.3rem 1rem 1rem 1rem}h3{padding:2rem 1rem 1rem 1rem;font-size:1rem}#rotate{animation:Rotate 5s linear infinite;transform-origin:center;transform-box:fill-box}@keyframes Rotate{from{transform:rotateZ(0deg)}to{transform:rotateZ(360deg)}}@media screen and (max-width: 768px){.song-container img{width:60%}}.library{position:fixed;top:0;left:0;width:20rem;height:100%;background-color:#eaf6fa;box-shadow:2px 2px 50px gray;overflow:scroll;transform:translateX(-100%);transition:all 0.5s ease;opacity:0}.library h2{padding:2rem}.library-song{display:flex;align-items:center;padding:1rem 2rem 1rem 2rem;cursor:pointer}.library-song img{width:30%}.library-song:hover{background-color:#fff2ff}.song-description{padding:1rem}.song-description h4{font-size:1rem;color:#646464}.song-description h5{font-size:0.75rem;color:#646464}*{scrollbar-width:thin;scrollbar-color:#d4d4d4 transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:#d1cfcf;border-radius:20px}.selected{background:pink}.active-library{transform:translateX(0%);opacity:1}@media screen and (max-width: 768px){.library{width:100%}}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;border:none;cursor:pointer;border:2px solid #414141;padding:0.5rem;transition:all 0.3s ease-in-out}nav button:hover{background:#414141;color:#fff}@media screen and (max-width: 768px){nav button{z-index:100}}

